import React from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

const PageNotFound = () => {
  const navigate = useNavigate();
  React.useEffect(() => {
    document.title = `404 | Page Not Found`;
  }, []);
  return (
    <Section>
      <Image
        src={require("../components/images/404_animation.gif")}
        alt="404 | Page not Found"
      />
      <Container>
        <Title>404 | Page not Found</Title>
        <Title>Sorry, This page no longer exist !</Title>
        <Text>
          We are very sorry for the inconvenient. It's looks like you are trying
          access a page that either has been deleted or never exist.
        </Text>
        <ButtonContainer>
          <Button
            onClick={() => {
              navigate(-1);
            }}
          >
            Back
          </Button>
        </ButtonContainer>
      </Container>
    </Section>
  );
};

export default PageNotFound;

export const Section = styled.section({
  width: "100%",
  height: "90vh",
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  cursor: "-moz-grab",
  marginTop: "80px",
});

export const Image = styled.img({
  width: "60%",
  height: "auto",
  "@media (min-width: 992px)": {
    width: "35%",
  },
});

export const Container = styled.div({
  padding: "0.8rem 1rem",
  display: "flex",
  flexDirection: "column",
  width: "60%",
  marginTop: "1rem",
  "@media (max-width: 678px)": {
    width: "90%",
  },
});

export const Title = styled.h1({
  textAlign: "center",
  fontWeight: "700",
  ":nth-child(2)": {
    fontSize: "1.2rem",
    marginTop: "1rem",
  },
  "@media (max-width: 678px)": {
    fontSize: "1.2rem",
  },
});

export const Text = styled.p({
  fontWeight: "500",
  fontSize: "1rem",
  textAlign: "center",
  color: "rgba(3,3,3, 0.6)",
});

export const Button = styled.button({
  padding: "0.5rem 3rem",
  border: "none",
  outline: "none",
  color: "#fff",
  fontWeight: "600",
  fontSize: "1rem",
  background: "#00824F",
  ":hover": {
    background: "#008E54",
  },
});

export const ButtonContainer = styled.div({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  padding: "2rem",
});
